<template>
  <div class="c-facedat-upload">
    <div class="u-upload" v-if="support">
      <template v-if="!done">
        <input
          class="u-input"
          type="file"
          id="face_file"
          accept=".dat,.jx3dat,.ini"
          @change="uploadData"
        />
        <el-button
          class="u-btn"
          type="primary"
          @click="selectData"
          icon="el-icon-upload2"
          >{{ btnText }}</el-button
        >
        <a
          v-if="type === 'face'"
          class="u-help"
          href="/tool/746"
          target="_blank"
        >
          <i class="el-icon-collection"></i> 
        </a>
      </template>
      <template v-else>
        <div class="u-result">
          <el-button
            class="u-reset"
            @click="resetData"
            icon="el-icon-refresh-left"
            ></el-button
          >
          <el-alert
            :title="'' + filename"
            type="success"
            show-icon
          ></el-alert>
        </div>
      </template>
    </div>
    <el-alert
      v-else
      class="u-notsupport"
      title=""
      type="warning"
      description=",chrome"
      show-icon
    ></el-alert>
  </div>
</template>

<script>
import { parseFace } from "./faceParser.js";
import { load } from "./bodyParser.js";
import types from "../assets/data/index.json";

export default {
  name: "Upload",
  props: {
    type: {
      type: String,
      default: "face",
    },
  },
  components: {},
  data: function () {
    return {
      support: true,

      file: "", //
      json: "", //json data
      object: "", //js object

      done: false,
      types: types.bodyMap,
    };
  },
  computed: {
    btnText() {
      return this.type === "face" ? "" : "";
    },
    filename: function () {
      return this.file && this.file.name;
    },
  },
  methods: {
    // 
    selectData: function (i) {
      let fileInput = document.getElementById("face_file");
      fileInput.dispatchEvent(new MouseEvent("click"));
    },
    uploadData: function (e) {
      let file = e.target.files[0];
      this.file = file;
      this.parseData(file);
    },
    // 
    parseData: function (file) {
      if (!file) return; //
      const vm = this;

      //  File  ArrayBuffer 
      let fr = new FileReader();
      fr.onload = function (e) {
        console.log("......");
        try {
          if (vm.type === "face") {
            vm.object = parseFace(e.target.result);
            if (vm.object.tBody) {
              return vm.$notify.error({
                title: "",
                message: "",
              });
            }
          } else {
            vm.object = load(e.target.result);
          }
        } catch (ex) {
          console.log(ex);
          vm.$notify.error({
            title: "",
            message: "",
          });
          vm.$emit("fail", {
            file: vm.file,
          });
          return;
        }
        // 
        if (vm.object) {
          try {
            vm.json = JSON.stringify(vm.object);
            vm.$notify({
              title: "",
              message: `${
                vm.types[vm.object.nRoleType.toString()]?.label || ""
              }${vm.type === "face" ? "" : ""}`,
              type: "success",
            });
            vm.done = true;
            vm.$emit("success", {
              file: vm.file,
              json: vm.json,
              object: vm.object,
            });
          } catch (e) {
            vm.$notify.error({
              title: "",
              message: "",
            });
            vm.$emit("fail", {
              file: vm.file,
            });
          }
        }
      };
      fr.onerror = function (e) {
        vm.$notify.error({
          title: "",
          message: "",
        });
      };
      fr.readAsArrayBuffer(file);
    },
    // 
    resetData: function () {
      this.done = false;

      this.file = "";
      this.json = "";
      this.object = "";
      this.$emit("success", {
        file: "",
        json: "",
        object: "",
      });
    },
  },
  filters: {},
  created: function () {},
  mounted: function () {
    this.support = !!window.FileReader;
  },
};
</script>

